export const SPECIALTIES_ADD = 'SPECIALTIES_ADD';
export const specialtiesAdd = (specialty) => ({ type: SPECIALTIES_ADD, specialty });

export const SPECIALTIES_EDIT = 'SPECIALTIES_EDIT';
export const specialtiesEdit = (specialty) => ({ type: SPECIALTIES_EDIT, specialty });


